const container = document.querySelector("#root");
// let : ÏÉàÎ°úÏö¥ Í∞íÏùÑ ÎÑ£ÏùÑ Ïàò ÏûàÏùå const : ÏÉàÎ°úÏö¥ Í∞íÏùÑ ÎÑ£ÏùÑ Ïàò ÏóÜÏùå (ÏÉÅÏàò)
const ajax = new XMLHttpRequest();
const content = document.createElement("div");
const NEWS_URL = "https://api.hnpwa.com/v0/news/1.json";
// Ïó¨Í∏∞ÏÑú @idÎäî ÎßàÌÇπÏùÑ ÌïòÎäî ÌïòÎÇòÏùò Î∞©Î≤ïÏúºÎ°ú idÎ∂ÄÎ∂ÑÏùÄ Ïó∞Í≤∞Îêú aÌÉúÍ∑∏Ïùò ÏïÑÏù¥ÌÖúÏóê Îî∞Îùº Îã¨ÎùºÏ†∏Ïïº ÌïòÍ∏∞ ÎïåÎ¨∏Ïóê ÎßàÌÇπÏùÑ Í±∏Ïñ¥ÎëîÎã§.
const CONTENT_URL = "https://api.hnpwa.com/v0/item/@id.json";
ajax.open("GET", NEWS_URL, false);
ajax.send();

// responseÌÉ≠ÏùÄ Î¨∏ÏûêÏó¥Î°ú ÎÇòÏôÄÏûàÍ∏∞ ÎïåÎ¨∏Ïóê ÏùºÎ†¨Î°ú ÎÇòÏôÄÏûàÏñ¥ Î≥¥Í∏∞ Î∂àÌé∏
// previewÌÉ≠ÏùÑ Î≥¥Î©¥ Î≥¥Í∏∞ Ìõ®Ïî¨ Ìé∏Ìïú Í≤ÉÏùÑ Ïïå Ïàò ÏûàÎã§.
// Í∑∏Î¶¨Í≥† ÏûêÎ∞îÏä§ÌÅ¨Î¶ΩÌä∏ÎèÑ Îç∞Ïù¥ÌÑ∞Î•º ÏÇ¨Ïö©Ìï† Îïå previewÌÉ≠Í≥º Í∞ôÏùÄ Îç∞Ïù¥ÌÑ∞ Íµ¨Ï°∞Î•º Îçî ÏÇ¨Ïö©ÌïòÍ∏∞ Ìé∏ÌïòÎã§.

// JSON.parse : Î¨∏ÏûêÏó¥ÏùÑ Îç∞Ïù¥ÌÑ∞Î•º json ÌòïÌÉúÎ°ú Î≥ÄÌôò
const newsFeed = JSON.parse(ajax.response);
const ul = document.createElement("ul");
window.addEventListener("hashchange", function () {
  content.innerHTML = "";
  const id = location.hash.substr(1); // substrÏùÄ Î¨∏ÏûêÏó¥ÏóêÏÑú Î∞∞Ïó¥ÏùÄ 0Î∂ÄÌÑ∞ ÏãúÏûëÌïòÍ≥† 1ÏùÑ Ï†ÅÏóàÍ∏∞ ÎïåÎ¨∏Ïóê 0Î≤àÏß∏ Î¨∏ÏûêÎäî Ïß§Î¶¨Í≥† 1~lastNum ÍπåÏßÄÏùò Î¨∏ÏûêÏó¥Ïù¥ Î∞òÌôòÎêúÎã§. Ï¶â #Í∞Ä Ïß§Î†§ÎÇòÍ∞ÑÎã§.
  ajax.open("GET", CONTENT_URL.replace("@id", id), false); // replaceÎ•º Ïù¥Ïö©Ìï¥ ÎßàÌÇπÏúºÎ°ú Ìï¥ÎÖº @idÎ•º locationÏúºÎ°ú Í∞ÄÏ†∏Ïò® idÎ°ú ÍµêÏ≤¥Ìï¥Ï§ÄÎã§.
  ajax.send();

  const newsContent = JSON.parse(ajax.response);
  const title = document.createElement("h1");
  title.innerHTML = newsContent.title;
  content.appendChild(title);
});

for (let i = 0; i < 10; i++) {
  const li = document.createElement("li");
  const a = document.createElement("a");
  a.href = `#${newsFeed[i].id}`;
  a.innerHTML = `${newsFeed[i].title} (${newsFeed[i].comments_count})`;
  li.appendChild(a);
  ul.appendChild(li);
}

// Ïó¨Í∏∞ÏÑú containerÎ°ú Î∞îÍøîÏ§Ä Ïù¥Ïú†Îäî document.querySelector("#root")Í∞Ä Î∞òÎ≥µÎêòÍ∏∞ ÎïåÎ¨∏Ïóê
// Í∏∞Î≥∏Ï†ÅÏúºÎ°ú ÏΩîÎìúÎäî Î∞òÎ≥µÎêòÎ©¥ Ï¢ãÏßÄ ÏïäÎã§. Í∑∏ Ïù¥Ïú†Îäî ÌòÑÏû¨ #rootÎ•º ÎÇòÎ¨¥ÎùºÍ≥† ÏÉùÍ∞ÅÌïòÍ≥† Í∑∏Ïóê ÌååÏÉùÎêòÎäî ÏΩîÎìúÎì§ÏùÑ ÎÇòÎ≠áÍ∞ÄÏßÄ ÎùºÍ≥† ÌïúÎã§Î©¥
// #rootÎ•º #appÏù¥ÎùºÎçòÍ∞Ä Îã§Î•∏ Ïù¥Î¶ÑÏúºÎ°ú Î≥ÄÍ≤ΩÌï¥Ï£ºÍ≤å ÎêòÎ©¥ Í∑∏Ïóê ÌååÏÉùÎêú ÎÇòÎ≠áÍ∞ÄÏßÄÎì§Ïù¥ ÎßéÏúºÎ©¥ ÎßéÏùÑÏàòÎ°ù Î™®Îì† ÏΩîÎìúÎ•º Îã§ Î∞îÍøîÏ§òÏïº ÌïòÍ∏∞ ÎïåÎ¨∏Ïóê Ï¢ãÏßÄ ÏïäÎã§.
// Îî∞ÎùºÏÑú containerÎ•º ÎßåÎì§Ïñ¥ Ï§åÏúºÎ°úÏç® Ïù¥Î¶ÑÏùÑ Î≥ÄÍ≤ΩÌï¥Ï£ºÍ≤å ÎêòÎçîÎùºÎèÑ containerÏùò #rootÎßå Î∞îÍøîÏ§åÏúºÎ°úÏç® Ïñ¥Îñ†Ìïú Î≤ÑÍ∑∏ÎèÑ ÏùºÏñ¥ÎÇòÏßÄ ÏïäÍ≤å Ìï¥Ï§ÄÎã§.
// Ï¶â #rootÎäî ÎÇòÎ¨¥ containerÎäî Ïó∞Í≤∞Ìï¥Ï£ºÎäî Ïª§ÎÑ•ÌÑ∞ Í∑∏Ïóê ÌååÏÉùÎêòÎäî ÏΩîÎìúÎäî ÎÇòÎ≠áÍ∞ÄÏßÄ ÎùºÍ≥† Î≥¥Î©¥ Îê† Í≤É Í∞ôÎã§.
container.appendChild(ul);
container.appendChild(content);
// ÏûêÎ∞îÏä§ÌÅ¨Î¶ΩÌä∏ ÎßåÏúºÎ°úÎäî ÌòÑÏû¨ ÌÅ¥Î¶≠Ïù¥ ÎêòÏóàÎäîÏßÄ Ïïå ÏàòÍ∞Ä ÏóÜÍ∏∞ ÎïåÎ¨∏Ïóê Î∏åÎùºÏö∞Ï†ÄÏùò Í∏∞Îä•Ïù∏ Ïù¥Î≤§Ìä∏ Í∏∞Îä•ÏùÑ ÏÇ¨Ïö©ÌïúÎã§.
